import { GridStackProvider, GridStackRender, GridStackRenderProvider, type ComponentDataType, type ComponentMap } from '@lib/gridstack';
import { type GridStackOptions } from 'gridstack';
import 'gridstack/dist/gridstack.css';
import React from 'react';

const CELL_HEIGHT = 50;
const BREAKPOINTS = [
  { c: 1, w: 700 },
  { c: 3, w: 850 },
  { c: 6, w: 950 },
  { c: 8, w: 1100 },
];

function Text({ content }: { content: string }) {
  return <div className="w-full h-full">{content}</div>;
}

const componentMap: ComponentMap = {
  Text,
  // ... other components here
};

// ! Content must be json string like this:
// { name: "Text", props: { content: "Item 1" } }
const gridOptions: GridStackOptions = {
  acceptWidgets: true,
  columnOpts: {
    breakpointForWindow: true,
    breakpoints: BREAKPOINTS,
    layout: "moveScale",
    columnMax: 12,
  },
  margin: 8,
  cellHeight: CELL_HEIGHT,
  subGridOpts: {
    acceptWidgets: true,
    columnOpts: {
      breakpoints: BREAKPOINTS,
      layout: "moveScale",
    },
    margin: 8,
    minRow: 2,
    cellHeight: CELL_HEIGHT,
  },
  children: [
    {
      id: "item1",
      h: 2,
      w: 2,
      x: 0,
      y: 0,
      noResize: false,
      content: JSON.stringify({
        name: "Text",
        props: { content: "Item 1" },
      } satisfies ComponentDataType<React.ComponentProps<typeof Text>>), // if need type check
    },
    {
      id: "item2",
      h: 2,
      w: 2,
      x: 2,
      y: 0,
      content: JSON.stringify({
        name: "Text",
        props: { content: "Item 2" },
      }),
    },
    {
      id: "sub-grid-1",
      h: 5,
      sizeToContent: true,
      subGridOpts: {
        acceptWidgets: true,
        cellHeight: CELL_HEIGHT,
        alwaysShowResizeHandle: false,
        column: "auto",
        minRow: 2,
        layout: "list",
        margin: 8,
        children: [
          {
            id: "sub-grid-1-title",
            locked: true,
            noMove: true,
            noResize: true,
            w: 12,
            x: 0,
            y: 0,
            content: JSON.stringify({
              name: "Text",
              props: { content: "Sub Grid 1 Title" },
            }),
          },
          {
            id: "item3",
            h: 2,
            w: 2,
            x: 0,
            y: 1,
            content: JSON.stringify({
              name: "Text",
              props: { content: "Item 3" },
            }),
          },
          {
            id: "item4",
            h: 2,
            w: 2,
            x: 2,
            y: 0,
            content: JSON.stringify({
              name: "Text",
              props: { content: "Item 4" },
            }),
          },
        ],
      },
      w: 12,
      x: 0,
      y: 2,
    },
  ],
};

export interface GridProps {}

export default function Grid({}: GridProps): React.JSX.Element {
  const [initialOptions] = React.useState(gridOptions);
  return <GridStackProvider initialOptions={initialOptions}>
    <GridStackRenderProvider>
      <GridStackRender componentMap={componentMap} />
    </GridStackRenderProvider>
  </GridStackProvider>;
}

