'use client'

import '@/managers/ToastQueueManager';
import '@/services/AccountService';
import '@/services/AuthenticationService';
import '@/services/ProjectService';
import '@/User';
import NavBar from '@components/navigation/NavBar';
import Toasts from '@components/Toasts';
import { Box } from '@mui/material';
import React from 'react';

export interface PageProps extends React.PropsWithChildren {
  navBarChildren?: React.ReactNode
}

export default function Page({ children, navBarChildren }: PageProps): React.JSX.Element {
  return <Box id='page' display='flex' flexDirection='column' height='100vh'>
    <Box flexGrow={0}>
      <NavBar>{navBarChildren}</NavBar>
    </Box>
    {children}
    <Toasts />
  </Box>;
}
